.header {
	.container {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
		-ms-flex-align: center;
		align-items: center;
		position: relative;
	}
	&__top {
		padding: 5px 0;
		.holder-form {
			-webkit-box-flex: 1.85;
			-webkit-flex-grow: 1.85;
			-ms-flex-positive: 1.85;
			flex-grow: 1.85;
			text-align: center;
		}
		.holder-search {
			position: relative;
			display: inline-block;
			input[type="search"] {
				padding: 8px 4px;
				display: block;
				width: 207px;
				font-size: 14px;
				font-family: 'Conv_AgendaMedium', Helvetica, sans-serif;
				color: #747474;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
				&:focus {
					outline: none;
				}
			}
		}
		.holder-search:after {
			content: '\f002';
			font: 12px/14px 'FontAwesome';
			position: absolute;
			right: 16px;
			top: 50%;
			-webkit-transform: translateY(-50%);
			-ms-transform: translateY(-50%);
			transform: translateY(-50%);
			color: $purple;
		}
		.holder-plan {
			-webkit-flex-basis: 270px;
			-ms-flex-preferred-size: 270px;
			flex-basis: 270px;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-ms-flex-align: center;
			-ms-grid-row-align: center;
			align-items: center;
			font-family: 'Conv_AgendaBold', Helvetica, sans-serif;
			font-size: 12px;
			text-transform: uppercase;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-pack: justify;
			-webkit-justify-content: space-between;
			-ms-flex-pack: justify;
			justify-content: space-between;
			.plan {
				a {
					margin: 0 20px 0 0;
					color: $purple;
					&:hover {
						color: #f48162;
					}
				}
			}
			.links {
				-webkit-flex-basis: 46px;
				-ms-flex-preferred-size: 46px;
				flex-basis: 46px;
			}

			[class^="icon-"], [class*=" icon-"] {
				/* use !important to prevent issues with browser extensions that change fonts */
				font-family: 'icomoon' !important;
				speak: none;
				font-style: normal;
				font-weight: normal;
				font-variant: normal;
				text-transform: none;
				line-height: 1;
				display: inline-block;

				/* Better Font Rendering =========== */
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
			}

			.icon-calendar:before {
				content: "\e900";
			}
			.icon-facebook:before {
				content: "\e901";
				font-size: 21px;
				color: $purple;
			}
			.icon-twitter:before {
				content: "\e902";
				font-size: 21px;
				color: $purple;
			}
			[class^="icon-"]:hover:before, [class*=" icon-"]:hover:before {
				color: #f48162;
			}

		}
	}
	&__logo {
		position: absolute;
		background: url('../img/logo.png') no-repeat;
		width: 186px;
		height: 95px;
		font-size: 0;
		line-height: 0;
		top: 0;
		left: 10px;
	}
	&__bottom {
		background: $purple;
		margin: 0 0 55px;
		.holder-nav {
			padding: 0 0 0 188px;
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-webkit-align-items: center;
			-ms-flex-align: center;
			align-items: center;
			-webkit-box-pack: justify;
			-webkit-justify-content: space-between;
			-ms-flex-pack: justify;
			justify-content: space-between;
			width: 100%;
		}
		.nav {
			font-family: 'Conv_AgendaMedium', Helvetica, sans-serif;
			font-size: 21px;
			text-transform: uppercase;
		}
		.nav-menu {
			li {
				display: inline-block;
				vertical-align: middle;
				margin: 0 20px 0 0;
				&:first-child a:after {
					content: none;
				}
				&:hover {
					text-shadow: 1px 0 1px;
					background: #ff9b4b;
					-webkit-box-shadow: 0 0 0 5px $white inset;
					box-shadow: 0 0 0 5px $white inset;
					a:after {
						content: none;
					}
				}
			}
			a {
				color: $white;
				padding: 15px 6px 15px 23px;
				display: inline-block;
				position: relative;
				&:after {
					content: '|';
					position: absolute;
					left: 0;
					top: 42%;
					-webkit-transform: translateY(-42%);
					-ms-transform: translateY(-42%);
					transform: translateY(-42%);
					color: #ed8e53;
					font-size: 15px;
				}
			}
		}
		.holder-book {
			font-family: 'Conv_AgendaBold', Helvetica, sans-serif;
			font-size: 18px;
			text-transform: uppercase;
			padding: 0 0 0 38px;
			position: relative;
			.book-form{
				bottom: auto;
				top: -12px;
				z-index: 5;
				right: -22px;
			}
		}
		.book {
			color: $white;
			position: relative;
			&:before {
				content: '\e900';
				font-family: 'icomoon' !important;
				font-size: 24px;
				position: absolute;
				top: 50%;
				left: -38px;
				-webkit-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
				transform: translateY(-50%);
				opacity: 0.5;
			}
		}
	}
}
.nav-menu {
	> li {
		position: relative;
	}
	ul {
		display: none;
		padding: 6px 5px 6px 209px;
		background: rgba(255, 255, 255, 0.8);
		position: absolute;
		z-index: 5;
		left: 0;
		width: 480px;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
		.img-holder {
			position: absolute;
			top: 0;
			left: 0;
			padding: 6px;
			background: $white;
			display: none;
		}
		.drop {
			display: block;
			margin-right: 6px;
			font-size: 14px;
			text-shadow: none;
			margin: 0 0 7px;
			&:first-child {
				.img-holder {
					display: block;
				}
			}
			&:hover {
				text-shadow: none;
				-webkit-box-shadow: none;
				box-shadow: none;
				cursor: pointer;
				.img-holder {
					display: block;
				}
			}
			a {
				color: #2a4a84;
				position: relative;
			}
		}
	}
	li:hover .sub-menu {
		display: block;
		.drop {
			&:hover a {
				color: $white;
				&:after {
					color: #653476;
				}
			}
		}
		a {
			padding: 7px 7px 7px 35px;
			&:after {
				content: '\f054';
				font: 9px/12px 'FontAwesome';
				position: absolute;
				left: 10px;
				top: 50%;
				-webkit-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
				transform: translateY(-50%);
				cursor: pointer;
			}
		}
	}
}